    <div v-if="!haveSlotIcon" class="lee-loading-icon" :class="thetype" :style="[sizeStyle,colorStyle]">
      <span></span><span></span>
    </div>
    <slot name="icon" v-else></slot>
    <div class="lee-loading-text"  v-if="haveSlot" :style="textColorStyle"><slot></slot></div>
